<template>
  <fish-table :columns="columns" :data="data"></fish-table>
</template>
<script>
  export default {
    name: 'demo-table-base',
    data () {
      return {
        columns: [{title: '', key: 'name'},
          {title: '', key: 'age'},
          {title: '', key: 'address'},
          {title: '',
            key: 'operate',
            render: (h, record, column) => h('a', '')}],
        data: [
          {name: '', age: 32, address: '1'},
          {name: '', age: 35, address: '5'},
          {name: '', age: 35, address: '5'},
          {name: '', age: 35, address: '5'}
        ]
      }
    }
  }
</script>


